import {
  Button,
  createMuiTheme,
  TextField,
  ThemeProvider,
} from "@material-ui/core";
import "./Search.css";
import SearchIcon from "@material-ui/icons/Search";
import React, { useEffect, useState } from "react";
import axios from "axios";
import CustomPagination from "../../components/Pagination/CustomPagination";
import SingleContent from "../../components/SingleContent/SingleContent";
import Fuse from "fuse.js";
import sha1 from 'crypto-js/sha1';

const Search = () => {
  const [searchText, setSearchText] = useState();
  const [page, setPage] = useState(1);
  const [content, setContent] = useState([]);
  const [numOfPages, setNumOfPages] = useState(1);
  const [cdn, setCdn] = useState('');
  const [db, setDb] = useState();
  const [allResult, setAllResult] = useState([]);

  const darkTheme = createMuiTheme({
    palette: {
      type: "dark",
      primary: {
        main: "#fff",
      },
    },
  });
  const perPage = 10;

  const fetchSearch = async () => {
    if (searchText === '' || typeof searchText === "undefined") {
      console.log("empty search text");
      return;
    }
    var searchContent = [];
    
    const tmpAllResult = db.search(searchText).map((result)=>result.item);
    setAllResult(tmpAllResult);
    setNumOfPages(Math.ceil(tmpAllResult.length/perPage));
    if (page === 1) {
      for(var i=0; i<Math.min(10, tmpAllResult.length); i++) {
        const resHash = sha1(tmpAllResult[i]).toString();
        try {
          const {data} = await axios.get(
            // `${cdn}output/${resHash}`
            `${process.env.REACT_APP_TEST_BASEDIR}output/${resHash}`
          );
          searchContent.push(data);
        }catch(err) {
          console.log(err);
        }
      }
      setContent(searchContent);
    } else {
      setPage(1);
    }
  };

  const initSearchJson = () => {
    if (db) {
      return;
    }
    if (cdn !== '') {
        axios.get(
          // `${cdn}search.json`
          `${process.env.REACT_APP_TEST_BASEDIR}search.json`
        ).then(function(res) {
          setDb(new Fuse(res['data']));
        }).catch(function(err) {
          
          console.log(err);
        });
    } else {
      axios.get(
        process.env.REACT_APP_CONFIG_URL
      ).then( async function(res) {
        setCdn(res.data.cdn);
        axios.get(
          `${process.env.REACT_APP_TEST_BASEDIR}search.json`
        ).then(function(res) {
          setDb(new Fuse(res['data']));
        }).catch(function(err) {
          
          console.log(err);
        });
      });
    }
  };

  const flushPage = async () => {
    console.log("in Flush page");
    var searchContent = []
    for(var i=(page-1)*perPage; i<Math.min(page*perPage, allResult.length); i++) {
      const resHash = sha1(allResult[i]).toString();
      try {
        const {data} = await axios.get(
          // `${cdn}output/${resHash}`
          `${process.env.REACT_APP_TEST_BASEDIR}output/${resHash}`
        );
        searchContent.push(data);
      }catch(err) {
        console.log(err);
      }
    }
    setContent(searchContent);
  };

  useEffect(()=>{
    initSearchJson();
  });

  useEffect(() => {
    window.scroll(0, 0);
    flushPage();
    // eslint-disable-next-line
  }, [page]);

  return (
    <div>
      <ThemeProvider theme={darkTheme}>
        <div className="search">
          <TextField
            style={{ flex: 1 }}
            className="searchBox"
            label="Search"
            variant="filled"
            onChange={(e) => setSearchText(e.target.value)}
          />
          <Button
            onClick={fetchSearch}
            variant="contained"
            style={{ marginLeft: 10 }}
          >
            <SearchIcon fontSize="large" />
          </Button>
        </div>
      </ThemeProvider>
      <div className="trending">
        {content &&
          content.map((c) => (
            <SingleContent
              key={c.slug}
              id={c.slug}
              poster={c.cover}
              title={c.title}
              date={c.update_at}
              media_type="video"
              vote_average='9.0'
              duration={c.duration}
              url={c.url}
              backurl="/search"
            />
          ))}
      </div>
      {numOfPages > 1 && (
        <CustomPagination setPage={setPage} page={page} numOfPages={numOfPages} />
      )}
    </div>
  );
};

export default Search;
