import "./SingleContent.css";

import { useHistory } from "react-router";


const SingleContent = ({
  id,
  poster,
  title,
  date,
  media_type,
  vote_average,
  duration,
  url,
  backurl,
}) => {
  const history = useHistory();
  const handlePlay = () =>{
    history.push({
      pathname: "/play",
      state: {"url": url, "backurl": backurl}
    });
  };
  function formatSeconds(value) {
    var theTime = parseInt(value);// 秒
    var theTime1 = 0;// 分
    var theTime2 = 0;// 小时
    if (theTime > 60) {
        theTime1 = parseInt(theTime / 60);
        theTime = parseInt(theTime % 60);
        if (theTime1 > 60) {
            theTime2 = parseInt(theTime1 / 60);
            theTime1 = parseInt(theTime1 % 60);
        }
    }
 
    var result = "" + parseInt(theTime);//秒
    if (10 > theTime > 0) {
        result = "0" + parseInt(theTime);//秒
    } else {
        result = "" + parseInt(theTime);//秒
    }
 
    if (10 > theTime1 > 0) {
        result = "0" + parseInt(theTime1) + ":" + result;//分，不足两位数，首位补充0，
    } else {
        result = "" + parseInt(theTime1) + ":" + result;//分
    }
    if (theTime2 > 0) {
        result = "" + parseInt(theTime2) + ":" + result;//时
    }
    return result;
  }
  duration = formatSeconds(duration);
  date  = new Date(date);
  const dateStr = date.getFullYear()+"-"+String((date.getMonth()+1)).padStart(2, '0')+ 
    "-"+String(date.getDate()).padStart(2, '0')+" "+String(date.getHours()).padStart(2, '0')+":"+
    String(date.getMinutes()).padStart(2, '0')+":"+String(date.getSeconds()).padStart(2, '0');
  return (
    <div
        className="media"
        style={{ cursor: "pointer" }}
        color="inherit"
        onClick={handlePlay}
      >
      <div className="imageBox">
        <img
          className="poster"
          src={poster}
          alt={title}
        />
        <span className="leftLogo">X100%</span>
        <span className="rightLogo">HD</span>
      </div>
      <div className="videoTimeBox">
        <span className="videoTime">{duration}</span>
      </div>
      
      <b className="title">{title}</b>
      <span className="subTitle">
        <span className="subTitle">添加时间：{dateStr}</span>
      </span>
    </div>
  );
};

export default SingleContent;
